@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f8faff;
    font-size: 14px;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 0.5em;
    color: rgba(0, 0, 0, 0.85);
    font-weight: 900;
    color: #253c77;
}


.content {
    margin: 0 auto;
    padding: 1em;
    max-width: 1200px;
}

@media (max-width: 500px) {
    .content {
        padding: 0.2em;
    }
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #253c77;
    padding: 15px 0;
}

.header__item {
    font-size: 1.4em;
    font-weight: 800;
}

@media (max-width: 475px) {
    .header {
        flex-direction: column;
    }

    .header__item {
        margin-bottom: 1em;

    }
}
.user {
    display: flex;
    align-items: center;
}

.icon {
    font-size: 2.2em;
    margin-left: 0.3em;
}

.user__image {
    width: 1.2em;
    height: 1.2em;
    margin-left: 10px;
    border-radius: 50%;
    font-size: 40px;
}

.user__icon-btn {
    background: transparent;
    padding: 0;
    border: none;
    margin-bottom: -0.4em;
    margin-left: 10px;
    transition: 0.1s;
    cursor: pointer;
}

.user__icon-btn:hover {
    color: #cccccc;
}

.user__icon-btn:active {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
}

.avatar {
    position: relative;
    width: 2.2em;
    height: 2.2em;
    background-color: #253c77;
    margin-left: 10px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
}

.profile-overlay .ant-drawer-content {
    width: 400px;
}

@media (max-width: 500px) {
    .profile-overlay .ant-drawer-content {
        width: 100%;
    }

}
.profile {
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

.profile .h2 {
    font-weight: 900;
}

.profile img {
    width: 100%;
}

.button {
    border: 2px solid transparent;
    margin: 0;
    padding: 1em;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
    line-height: normal;
    border-radius: 0.2em;
    font-weight: 800;
    box-shadow: 0 0 0.2em rgba(41, 61, 113, .1);
    transition: 0.2s;
    cursor: pointer;
    font-size: 1.1em;
}

.primary {
    background-color: #253c77;
    color: #fff;
}

.light {
    background-color: #1890ff;
    color: #fff;
}

.danger {
    background-color: #ff7875;
    color: #fff;
}

.outline {
    border: 2px solid #253c77;
    color: #253c77;
}

.full {
    width: 100%;
}

.content {
    width: auto;
}

.button:hover {
    opacity: 0.7;
}


.button:active {
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    box-shadow: none;
}
.account {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: "card card select" "history history .";
    color: #253c77;
    margin-top: 2em;
}

.account__select {
    grid-area: select;
    margin-left: 3em;
}

.account__history {
    margin-top: 2em;
    grid-area: history;
}

@media (max-width: 800px) {
    .account {
        grid-template-areas: "select" "card" "history";
    }

    .account__select {
        margin-left: 0;
        margin-bottom: 2em;
    }
}
.account__card {
    grid-area: card;
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
}

.account__number {
    font-size: 1.4em;
    font-weight: 600;
}

.account__balance {
    font-size: 1.7em;
    font-weight: 800;
    margin-top: 10px;
}

.account__date {
    margin: 1em;
    padding: 0.5em;
    border-radius: 0.2em;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.account__table {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin-right: 2em;
    margin-top: 1em;
    border-radius: 0.5em;
    width: 100%;
}

.account__table table {
    width: 100%;
    border-collapse: collapse;

}

.account__table table tbody tr:nth-child(even) {
    background-color: #f8faff;

}

.account__table table thead {
    border-bottom: 2px solid rgb(41, 61, 113);
    font-size: 1.3em;
    font-weight: 800;
}

.account__table thead td {
    padding: 0.4em 1em;
}

.account__table tbody td {
    padding: 1.2em;
}

@media (max-width: 500px) {
    .account__table table, thead, tbody, th, td, tr {
        display: block;
    }

    .account__table table thead {
        border: none;
    }

    .account__table thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }


    .account__table tbody td {
        border: none;
        position: relative;
        padding: 0.2em 0.2em 0.2em 50%;
    }

    .account__table td:before {
        /* Now like a table header */
        position: absolute;
        /* Top/left values mimic padding */
        top: 0.2em;
        left: 0.2em;
        width: 45%;
        padding-right: 0.2em;
        white-space: nowrap;
    }

    .account__table {
        padding: 0.3em;
    }

    .account__table table tbody tr:nth-child(even) {
        background-color: #f8faff;
        border-top: 2px solid #253c77;
        margin-top: 1em;
        margin-bottom: 1em;
        border-bottom: 2px solid #253c77;
    }


    .account__table tbody td:nth-of-type(1):before {
        content: "Дата";
    }

    .account__table tbody td:nth-of-type(2):before {
        content: "Тип операции";
    }

    .account__table tbody td:nth-of-type(3):before {
        content: "Сумма операции";
    }

    .account__table-full tbody td:nth-of-type(4):before {
        content: "Счёт отправителя";
    }

    .account__table-full tbody td:nth-of-type(5):before {
        content: "Счёт получателя";
    }

}
.input {
    display: block;
    width: 100%;
    background-color: #eff4ff;
    padding: 0.7em;
    font-size: 1.3em;
    font-weight: 600;
    color: #253c77;
    margin-bottom: 2em;
    border: 3px solid #fff;
    transition: 0.3s;
    border-radius: 0.2em;
}

.input:focus {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border: 3px solid #253c77;
}

.input:invalid {
    border: 3px solid rgba(0, 0, 0, 0);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
    background-color: #fff1eb;
}

.label {
    display: block;
    margin: 0.7em 0;
    font-size: 1.2em;
    text-align: left;
    color: #253c77;
}
.form {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin: 3em auto;
    border-radius: 0.5em;
    width: 500px;
    text-align: center;
}


.form__btn {
    width: 100%;
    font-weight: 800;
    margin-bottom: 1em;
}

.form__header {
    margin: 0 0 1em;
    color: #253c77;
    font-weight: 800;
    text-align: left;
}

@media (max-width: 600px) {
    .form {
        width: 100%;
        padding: 1em;
        margin: 1em auto;
    }
}
.requisite-container {
    display: grid;
    width: 100%;
    position: relative;
    grid-template-rows: auto;
    grid-template-areas: "form form templates";
}

.requisite-container__inputs {
    grid-area: form;
}

.requisite-container__templates {
    grid-area: templates;
}

@media (max-width: 600px) {
    .requisite-container {
        display: grid;
        width: 100%;
        position: relative;
        grid-template-rows: auto;
        grid-template-areas: "form" "templates";
    }
}
.account-template-card {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
    margin-top: 3em;
    cursor: pointer;
    transition: 0.2s;
}

.account-template-card:hover {
    opacity: 0.8;
}

.account-template-card:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}


.custom-file-input::-webkit-file-upload-button {
    visibility: hidden;
}

.file-input {
    padding: 1em;
    font: inherit;
    line-height: normal;
    border-radius: 0.2em;
    font-weight: 800;
    box-shadow: 0 0 0.2em rgba(41, 61, 113, .1);
    transition: 0.2s;
    cursor: pointer;
    font-size: 1.1em;
    border: 2px solid #253c77;
    width: 100%;
    color: #253c77;
    margin-bottom: 2em;
}
