.form {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    margin: 3em auto;
    border-radius: 0.5em;
    width: 500px;
    text-align: center;
}


.form__btn {
    width: 100%;
    font-weight: 800;
    margin-bottom: 1em;
}

.form__header {
    margin: 0 0 1em;
    color: #253c77;
    font-weight: 800;
    text-align: left;
}

@media (max-width: 600px) {
    .form {
        width: 100%;
        padding: 1em;
        margin: 1em auto;
    }
}