.requisite-container {
    display: grid;
    width: 100%;
    position: relative;
    grid-template-rows: auto;
    grid-template-areas: "form form templates";
}

.requisite-container__inputs {
    grid-area: form;
}

.requisite-container__templates {
    grid-area: templates;
}

@media (max-width: 600px) {
    .requisite-container {
        display: grid;
        width: 100%;
        position: relative;
        grid-template-rows: auto;
        grid-template-areas: "form" "templates";
    }
}