.account__card {
    grid-area: card;
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
}

.account__number {
    font-size: 1.4em;
    font-weight: 600;
}

.account__balance {
    font-size: 1.7em;
    font-weight: 800;
    margin-top: 10px;
}
