.input {
    display: block;
    width: 100%;
    background-color: #eff4ff;
    padding: 0.7em;
    font-size: 1.3em;
    font-weight: 600;
    color: #253c77;
    margin-bottom: 2em;
    border: 3px solid #fff;
    transition: 0.3s;
    border-radius: 0.2em;
}

.input:focus {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border: 3px solid #253c77;
}

.input:invalid {
    border: 3px solid rgba(0, 0, 0, 0);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
    background-color: #fff1eb;
}

.label {
    display: block;
    margin: 0.7em 0;
    font-size: 1.2em;
    text-align: left;
    color: #253c77;
}