.user {
    display: flex;
    align-items: center;
}

.icon {
    font-size: 2.2em;
    margin-left: 0.3em;
}

.user__image {
    width: 1.2em;
    height: 1.2em;
    margin-left: 10px;
    border-radius: 50%;
    font-size: 40px;
}

.user__icon-btn {
    background: transparent;
    padding: 0;
    border: none;
    margin-bottom: -0.4em;
    margin-left: 10px;
    transition: 0.1s;
    cursor: pointer;
}

.user__icon-btn:hover {
    color: #cccccc;
}

.user__icon-btn:active {
    transform: scale(0.6);
}

.avatar {
    position: relative;
    width: 2.2em;
    height: 2.2em;
    background-color: #253c77;
    margin-left: 10px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
}

.profile-overlay .ant-drawer-content {
    width: 400px;
}

@media (max-width: 500px) {
    .profile-overlay .ant-drawer-content {
        width: 100%;
    }

}