.account {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: "card card select" "history history .";
    color: #253c77;
    margin-top: 2em;
}

.account__select {
    grid-area: select;
    margin-left: 3em;
}

.account__history {
    margin-top: 2em;
    grid-area: history;
}

@media (max-width: 800px) {
    .account {
        grid-template-areas: "select" "card" "history";
    }

    .account__select {
        margin-left: 0;
        margin-bottom: 2em;
    }
}