.account-template-card {
    background-color: #ffffff;
    box-shadow: 0 0 1rem rgba(41, 61, 113, .1);
    padding: 2em 3em;
    border-radius: 0.5em;
    margin-top: 3em;
    cursor: pointer;
    transition: 0.2s;
}

.account-template-card:hover {
    opacity: 0.8;
}

.account-template-card:active {
    transform: scale(0.98);
}

