.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #253c77;
    padding: 15px 0;
}

.header__item {
    font-size: 1.4em;
    font-weight: 800;
}

@media (max-width: 475px) {
    .header {
        flex-direction: column;
    }

    .header__item {
        margin-bottom: 1em;

    }
}