.custom-file-input::-webkit-file-upload-button {
    visibility: hidden;
}

.file-input {
    padding: 1em;
    font: inherit;
    line-height: normal;
    border-radius: 0.2em;
    font-weight: 800;
    box-shadow: 0 0 0.2em rgba(41, 61, 113, .1);
    transition: 0.2s;
    cursor: pointer;
    font-size: 1.1em;
    border: 2px solid #253c77;
    width: 100%;
    color: #253c77;
    margin-bottom: 2em;
}