.button {
    border: 2px solid transparent;
    margin: 0;
    padding: 1em;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
    line-height: normal;
    border-radius: 0.2em;
    font-weight: 800;
    box-shadow: 0 0 0.2em rgba(41, 61, 113, .1);
    transition: 0.2s;
    cursor: pointer;
    font-size: 1.1em;
}

.primary {
    background-color: #253c77;
    color: #fff;
}

.light {
    background-color: #1890ff;
    color: #fff;
}

.danger {
    background-color: #ff7875;
    color: #fff;
}

.outline {
    border: 2px solid #253c77;
    color: #253c77;
}

.full {
    width: 100%;
}

.content {
    width: auto;
}

.button:hover {
    opacity: 0.7;
}


.button:active {
    transform: scale(0.99);
    box-shadow: none;
}